* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

body {
    margin: 0;
    padding: 30px;
    color: #2f2f2f;
    font-size: 16px;
    line-height: 1.625;
    letter-spacing: .5px;
    background-color: #f2f2f2;
    font-family: 'Roboto', sans-serif;
    -webkit-font-smoothing: antialiased;
    overflow-x: hidden;
}

::-moz-selection {
    color: #fff;
    background: #2196f3;
}

::selection {
    color: #fff;
    background: #2196f3;
}

.form-control:focus,
