.personal-details-area {
    margin-bottom: 60px;
}

.aboutme-section ul.service-list li {
    width: 200px;
    height: 200px;
    padding: 30px;
    overflow: hidden;
    position: relative;
    margin-right: 30px;
    margin-bottom: 30px;
    background-color: #ffffff;
    -webkit-box-shadow: 0 10px 40px 0 rgba(1, 1, 1, 0.1);
            box-shadow: 0 10px 40px 0 rgba(1, 1, 1, 0.1);
}

.aboutme-section ul.service-list li:before {
    left: 0;
    right: 0;
    bottom: 0;
    content: '';
    width: 100%;
    height: 2px;
    position: absolute;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

.aboutme-section ul.service-list li > i {
    top: 40px;
    left: 40px;
    font-size: 40px;
    position: absolute;
}

.aboutme-section ul.service-list li > span.service-name {
    left: 40px;
    bottom: 40px;
    color: #111111;
    font-size: 20px;
    position: absolute;
    text-transform: capitalize;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

.aboutme-section ul.service-list li > a.more {
    top: 40px;
    width: 45px;
    right: -100%;
    height: 35px;
    color: #ffffff;
    line-height: 35px;
    text-align: center;
    position: absolute;
    background-color: #2196f3;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

