    height: 100%;
    color: #111111;
    display: block;
    font-size: 24px;
    line-height: 100px;
}

/* - 5 - footer-section end
==================================================================================================== */

/* - 6 - Start of Chooser page
==================================================================================================== */
.chooser-head {
    text-align: center;
}

.chooser-head {
    text-align: center;
    padding: 200px;
    background-color: #26a69a;
}

.chooser-title h2 {
    font-size: 50px;
    font-weight: 700;
    color: #ffffff;
    padding-bottom: 15px;
}

.chooser-head .title-dec {
    font-size: 24px;
    color: #ffffff;
}

.view-demo-btn {
    margin-top: 30px;
}

body.clearfix.chooser {
    margin: 0;
    padding: 0;
}

.demo-title h3 {
    font-size: 30px;
    letter-spacing: 2px;
    line-height: 24px;
    margin: 0;
    position: relative;
    text-transform: uppercase;
    padding-bottom: 13px;
    font-weight: 700;
}

.demo-title {
    position: relative;
    text-align: center;
    padding-top: 70px;
    margin-bottom: 45px;
}

.demo-title::before {
    width: 64px;
    height: 1px;
    background: #dddddd;
    margin-left: -32px;
    bottom: 0;
    left: 50%;
    content: "";
    position: absolute;
}

.dmeo-item-title h3 {
    font-size: 18px;
    font-weight: 700;
    font-style: italic;
    text-align: center;
    margin-top: 15px;
}

.dmeo-item-list {
    -webkit-box-shadow: 0 0 15px rgba(0, 0, 0, 0.15);
            box-shadow: 0 0 15px rgba(0, 0, 0, 0.15);
    padding-bottom: 10px;
    text-align: center;
}

.dmeo-item-list.half {
    text-align: center;
    max-width: 370px;
    margin: 0 auto;

}

.preview i {
    font-style: italic;
    font-weight: 700;
    color: #ffffff;
}

.preview i span {
    margin-left: 10px;
}

.dmeo-item-list {
    position: relative;
    margin-bottom: 45px;
}

.img-ovelay {
    position: relative;
}

.overlay {
    position: absolute;
    top: 0;
    height: 98%;
    width: 100%;
    background-color: rgba(32, 148, 243, 0.8);
    opacity: 0;
    -webkit-transition: .3s all ease-in-out;
    -o-transition: .3s all ease-in-out;
    transition: .3s all ease-in-out;
}

.preview {
    position: absolute;
    top: 30%;
    left: 0;
    right: 0;
    opacity: 0;
    -webkit-transition: .3s all ease-in-out;
    -o-transition: .3s all ease-in-out;
    transition: .3s all ease-in-out;
    text-align: center;
}

.dmeo-item-list:hover .overlay {
    opacity: 1;
}

.dmeo-item-list:hover .preview {
    opacity: 1;
    top: 40%;
}

.other-demo-item {
    background-color: #ffffff;
    padding-bottom: 70px;
}

.choose-demo,
.other-demo-item-2 {
    padding-bottom: 70px;
}

/* - 6 - End of Chooser page
==================================================================================================== */

.content {
    width: 75%;
    float: right;
    position: relative;
    padding-left: 30px;
}



